body {
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: calc(100vh - 10em);
  position: bottom;
  background-color: #252525;
  background-image: -webkit-radial-gradient(#2222, #222), -webkit-linear-gradient(white, black) , url(/max-garber-website/static/media/background-winter-beach.942afcb1.jpeg);
  background-image: radial-gradient(#2222, #222), linear-gradient(white, black) , url(/max-garber-website/static/media/background-winter-beach.942afcb1.jpeg);
  background-blend-mode: darken, color , normal;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 70% top;
  color: White;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Toolbar {
  position: fixed;
  z-index: 100;
  width: 70%;
  height: 3em;
  top: 1em;
  background-color: #333;
  background-image: -webkit-linear-gradient(left, #111, #333);
  background-image: linear-gradient(90deg, #111, #333);
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s; }
  .Toolbar ul {
    display: -webkit-flex;
    display: flex; }
    .Toolbar ul li {
      padding-left: 2em;
      list-style: none; }
  .Toolbar a {
    text-decoration: none;
    text-align: center;
    color: white; }
  .Toolbar .active {
    font-weight: bold; }
  .Toolbar:after {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    border-top: 0em solid #333;
    border-bottom: 3em solid transparent;
    border-left: 4em solid #333;
    top: 0;
    right: -4em; }
  .Toolbar.collapsed {
    -webkit-transform: translateX(-95%);
            transform: translateX(-95%);
    background-image: -webkit-linear-gradient(left, #111, #111);
    background-image: linear-gradient(90deg, #111, #111); }
    .Toolbar.collapsed:after {
      border-top: 0em solid #111;
      border-left: 4em solid #111; }

main {
