.ProjectContainer{
  border-style: solid;
  border-radius: 5px;
  border-width: 2px;
  border-color: #333;
  width: 60%;
  margin: 1em auto;
  color: black;
  display: flex;
  flex-wrap: wrap;
  // padding: 1em;
  // box-shadow: 1px 1px 2px 1px #444;
  .Name{
    border: none;
    color: white;
    font-weight: bolder;
    background-color: #263b50;
    flex: 1 1 25%;
    cursor: pointer;
  }
  .Description{
    box-sizing: border-box;
    background-color:  #275788;
    color: white;
    width: 100%;
    padding: .5em 1em;
    flex: 1 1 75%;
  }
  .Content{
    border-top: solid 1px #333;
    padding: 0.5em 2em;
    background-color:  white;
    flex: 1 1 100%;
  }
}
